// Theme context
const ThemeContext = React.createContext("sunrise"); // "sunrise" | "sunset"

// ---------------- UI Primitives (FROSTED) ----------------
const Card = ({ children, className = "", noClip = false }) => {
  const theme = useContext(ThemeContext);
  const isNight = theme === "sunset";
  const frostBg = isNight ? "bg-white/10" : "bg-white/20";
  const borderClr = isNight ? "border-white/20" : "border-white/40";
  const shadow = "shadow-[0_6px_24px_rgba(0,0,0,0.08)]";
  const overflowCls = noClip ? "overflow-visible" : "overflow-hidden";
  return (
    <div
      className={`relative rounded-3xl border ${borderClr} ${frostBg} bg-clip-padding backdrop-blur-xl backdrop-saturate-150 ${shadow} ${overflowCls} ${className}`}
    >
      {children}
    </div>
  );
};

const CardHeader = ({ title, subtitle, right, dragHandle }) => {
  const theme = useContext(ThemeContext);
  const border = theme === "sunset" ? "border-white/15" : "border-white/45";
  const textMuted = theme === "sunset" ? "text-white/70" : "text-gray-600";
  return (
    <div className={`flex items-center justify-between p-4 border-b ${border}`}>
      <div className="flex items-center gap-3">
        {dragHandle}
        <div>
          {title && (
            <h3 className={`text-lg font-semibold leading-tight ${theme === "sunset" ? "text-white" : ""}`}>{title}</h3>
          )}
          {subtitle && <p className={`text-xs ${textMuted} mt-1`}>{subtitle}</p>}
        </div>
      </div>
      {right}
    </div>
  );
};

const CardBody = ({ children, className = "" }) => <div className={`p-4 ${className}`}>{children}</div>;

const Button = ({ children, onClick, className = "", variant = "primary", type = "button", disabled = false }) => {
  const theme = useContext(ThemeContext);
  const primaryGradient = theme === "sunset"
    ? `bg-gradient-to-r from-[${BRAND.red}] to-[${BRAND.deepBlue}] text-white`
    : `bg-gradient-to-r from-[rgba(246,229,0,0.6)] to-[rgba(57,180,232,0.6)] text-gray-900`;
  const ghostBg = theme === "sunset" ? "bg-transparent hover:bg-white/15 text-white" : "bg-transparent hover:bg-white/50";
  const neutralBg = theme === "sunset" ? "bg-white/10 border border-white/20 hover:bg-white/15 text-white" : "bg-white/60 hover:bg-white";
  const dangerBg = theme === "sunset"
    ? "bg-red-600/80 text-white hover:bg-red-600 border border-red-500/20"
    : "bg-red-600 text-white hover:bg-red-700";

  return (
    <button
      type={type}
      onClick={onClick}
      disabled={disabled}
      className={`inline-flex items-center gap-2 rounded-2xl px-4 py-2 text-sm font-medium transition-all active:scale-[.99] ${variant === "primary"
        ? primaryGradient
        : variant === "ghost"
          ? ghostBg
          : variant === "danger"
            ? dangerBg
            : neutralBg
        } ${disabled ? "opacity-50 pointer-events-none" : ""} ${className}`}
    >
      {children}
    </button>
  );
};

