// ---------------- API ----------------
// Remove the old API_BASE constant and use API_BASE_URL from config
async function apiFetchOpps() {
  const url = `${API_BASE_URL}/opportunities`;
  console.log("Fetching opportunities from:", url);

  try {
    const res = await fetch(url, {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
      credentials: 'include',
      mode: 'cors'
    });

    if (!res.ok) {
      const text = await res.text();
      throw new Error(`GET ${url} failed: ${res.status} ${text}`);
    }

    const data = await res.json();
    console.log("Raw API response:", data);

    // Transform SAP Datasphere fields to frontend format
    return data.map(record => ({
      // Core fields
      id: record.OPPORTUNITY_ID || record.id,
      title: record.CUSTOMER_NAME ? `${record.CUSTOMER_NAME} - ${record.PRODUCT}` : record.title,
      amount: Number(record.PIPELINE_PROJECTED_REVENUE || record.amount || 0),
      status: record.SALES_STAGE || record.status || 'Lead: No Current Product Solution',
      owner: record.SALES_LEAD || record.owner || 'system@doleintl.com',
      createdAt: record.LIKELY_START_DATE ? new Date(record.LIKELY_START_DATE) : new Date(),
      closeDate: record.END_DATE ? new Date(record.END_DATE) : new Date(),

      // Mapped fields for your UI
      customerName: record.CUSTOMER_NAME,
      product: record.PRODUCT,
      doleSalesLead: record.SALES_LEAD,
      salesTeam: record.SALES_TEAM,
      industrySegment: record.INDUSTRY_SEGMENT,
      salesStage: record.SALES_STAGE,
      opportunityType: record.OPPORTUNITY_TYPE,
      materialId: record.MATERIAL_ID,
      estimatedVolume: record.ESTIMATED_VOLUME,
      pipelineProjectedRevenue: record.PIPELINE_PROJECTED_REVENUE,

      // Include all original fields for debugging
      ...record
    }));
  } catch (e) {
    console.error("Failed to fetch opportunities:", e);
    throw e;
  }
}
async function apiCreateOpp(body) {
  try {
    // Prepare the complete payload with all field mappings
    // Handle both naming conventions (snake_case from form and camelCase from existing data)
    const payload = {
      // Generate opportunity ID if not provided
      opportunity_ID: body.opportunity_ID || `OPP-${Date.now()}`,

      // Core fields
      title: body.title || `${body.customer_Name || body.customerName} - ${body.product}`,
      amount: Number(body.pipeline_Projected_Revenue || body.pipelineProjectedRevenue || body.amount || 0),
      status: body.sales_Stage || body.salesStage || body.status || 'Lead: No Current Product Solution',
      owner: body.owner || 'system@doleintl.com', // Fixed: use default value instead of undefined currentUser

      // Dates - ensure proper ISO format
      createdAt: body.createdAt || new Date().toISOString(),
      closeDate: body.closeDate || body.end_Date || body.endDate || new Date().toISOString(),

      // Opportunity & Ownership fields
      customer_Name: body.customer_Name || body.customerName || '',
      sales_Lead: body.sales_Lead || body.doleSalesLead || body.salesLead || '',
      sales_Team: body.sales_Team || body.salesTeam || '',
      industry_Segment: body.industry_Segment || body.industrySegment || '',
      other_Segment: body.other_Segment || body.otherSegment || '',
      sales_Stage: body.sales_Stage || body.salesStage || body.status || 'Lead: No Current Product Solution',
      opportunity_Type: body.opportunity_Type || body.opportunityType || 'New Business',
      opportunity_Summary: body.opportunity_Summary || body.opportunitySummary || '',

      // Product & Material fields
      product: body.product || '',
      like_Product: body.like_Product || body.likeProduct || '',
      material_ID: body.material_ID || body.materialId || '',
      product_Category: body.product_Category || body.productCategory || '',
      base_UoM: body.base_UoM || body.baseUoM || body.materialBaseUnit || 'Case',
      material_Weight: body.material_Weight || body.materialWeight || body.materialNetWeightLbs || '',
      product_Source_Location: body.product_Source_Location || body.productSourceLocation || '',
      likely_Distributors: body.likely_Distributors || body.likelyDistributors || '',

      // Volume & Units fields
      estimated_Volume: body.estimated_Volume || body.estimatedVolume || '',
      uoM: body.uoM || body.uom || 'Case',
      case_Volume_Converted: body.case_Volume_Converted || body.caseVolumeConverted || body.caseVolume || '',
      opportunity_Volume_Input: body.opportunity_Volume_Input || body.opportunityVolumeInput || '',
      days_30_Ship: body.days_30_Ship || body.days30Ship || 'N',

      // Pricing & Financial Impact fields
      material_Projected_Price: body.material_Projected_Price || body.materialProjectedPrice || '',
      equivalized_Pipeline_LBS: body.equivalized_Pipeline_LBS || body.equalizedPipelineLbs || '',
      pipeline_Projected_Revenue: body.pipeline_Projected_Revenue || body.pipelineProjectedRevenue || body.amount || '',

      // Timing & Lifecycle fields
      likely_Start_Date: body.likely_Start_Date || body.likelyStartDate || body.createdAt || new Date().toISOString(),
      annual_Or_LTO: body.annual_Or_LTO || body.annualOrLTO || 'Annual',
      end_Date: body.end_Date || body.endDate || body.closeDate || new Date().toISOString(),
      last_Meeting_Date: body.last_Meeting_Date || body.lastMeetingDate || '',
      next_Step_Description: body.next_Step_Description || body.nextStepDescription || '',

      // Outcome & Notes fields
      win_Loss_Reason_Code: body.win_Loss_Reason_Code || body.winLossReasonCode || '',
      win_Loss_Comments: body.win_Loss_Comments || body.winLossComments || '',

      // Support & Enablement fields
      samples_Needed: body.samples_Needed || body.samplesNeeded || 'N',
      samples_Support_Status: body.samples_Support_Status || body.samplesSupportStatus || '',
      culinary_Support_Needed: body.culinary_Support_Needed || body.culinarySupportNeeded || 'N',
      culinary_Support_Description: body.culinary_Support_Description || body.culinarySupportDescription || '',
      culinary_Support_Status: body.culinary_Support_Status || body.culinarySupportStatus || '',
      innovation_Support_Needed: body.innovation_Support_Needed || body.innovationSupportNeeded || 'N',
      innovation_Need_Description: body.innovation_Need_Description || body.innovationNeedDescription || '',
      innovation_Support_Status: body.innovation_Support_Status || body.innovationSupportStatus || '',

      // Additional fields that might come from the form
      total_Units: body.total_Units || body.units2023 || '',
    };

    console.log('Sending opportunity to SAP Datasphere:', payload);

    const res = await fetch(`${API_BASE_URL}/opportunities`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      credentials: 'include',  // Include cookies for BTP authentication
      mode: 'cors',  // Explicitly enable CORS mode
      body: JSON.stringify(payload),
    });

    if (!res.ok) {
      const errorText = await res.text();
      console.error('SAP Datasphere API error:', errorText);
      throw new Error(`Failed to create opportunity: ${res.status} ${errorText}`);
    }

    const created = await res.json();
    console.log('Successfully created opportunity in SAP Datasphere:', created);

    // Return the created opportunity with normalized field names for the UI
    const normalizedOpp = {
      id: created.id || created.opportunity_ID || payload.opportunity_ID,
      title: created.title || payload.title,
      amount: Number(created.amount || created.pipeline_Projected_Revenue || payload.pipeline_Projected_Revenue),
      status: created.status || created.sales_Stage || payload.sales_Stage,
      owner: created.owner || payload.owner,
      createdAt: new Date(created.createdAt || payload.createdAt),
      closeDate: new Date(created.closeDate || created.end_Date || payload.end_Date),

      // Include all the original fields for consistency
      customerName: created.customer_Name || payload.customer_Name,
      product: created.product || payload.product,
      doleSalesLead: created.sales_Lead || payload.sales_Lead,
      salesTeam: created.sales_Team || payload.sales_Team,
      industrySegment: created.industry_Segment || payload.industry_Segment,
      salesStage: created.sales_Stage || payload.sales_Stage,
      opportunityType: created.opportunity_Type || payload.opportunity_Type,
      materialId: created.material_ID || payload.material_ID,
      estimatedVolume: created.estimated_Volume || payload.estimated_Volume,
      pipelineProjectedRevenue: created.pipeline_Projected_Revenue || payload.pipeline_Projected_Revenue,

      // Include all fields from the created response
      ...created,

      // Ensure these fields are present for the UI
      opportunity_ID: created.opportunity_ID || payload.opportunity_ID,
      customer_Name: created.customer_Name || payload.customer_Name,
      sales_Lead: created.sales_Lead || payload.sales_Lead,
      sales_Team: created.sales_Team || payload.sales_Team,
    };

    return normalizedOpp;

  } catch (error) {
    console.error('Failed to create opportunity in SAP Datasphere:', error);

    // Add more specific error handling for CORS
    if (error.message.includes('Failed to fetch') || error.message.includes('CORS')) {
      console.error('CORS or network issue detected.');
      console.error('Current origin:', window.location.origin);
      console.error('Target API:', API_BASE_URL);
    }

    // If SAP Datasphere fails, create a local opportunity as fallback
    // This ensures the UI continues to work even if the backend is down
    // Fixed: Remove reference to undefined 'opps' variable
    const fallbackId = Math.floor(Math.random() * 10000) + 1;
    const fallbackOpp = {
      id: fallbackId,
      title: body.title || `${body.customer_Name} - ${body.product}`,
      amount: Number(body.pipeline_Projected_Revenue || body.amount || 0),
      status: body.sales_Stage || body.status || 'Lead: No Current Product Solution',
      owner: body.owner || 'system@doleintl.com', // Fixed: use default value
      createdAt: new Date(),
      closeDate: body.end_Date ? new Date(body.end_Date) : new Date(),
      ...body,
    };

    console.warn('Using local fallback for opportunity creation:', fallbackOpp);
    return fallbackOpp;
  }
}

async function apiDeleteOpps(ids = []) {
  try {
    const res = await fetch(`${API_BASE_URL}/opportunities/bulk-delete`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      credentials: 'include',  // Added for consistency
      mode: 'cors',  // Added for consistency
      body: JSON.stringify({ ids }),
    });

    if (res.ok) {
      const result = await res.json();
      console.log('Bulk delete successful:', result);
      return true;
    }

    // If bulk delete fails, try individual deletes
    console.warn('Bulk delete failed, trying individual deletes');
  } catch (error) {
    console.error('Bulk delete error:', error);
  }

  try {
    // Fallback to individual deletes
    await Promise.all(ids.map((id) =>
      fetch(`${API_BASE_URL}/opportunities/${id}`, {
        method: "DELETE",
        credentials: 'include',
        mode: 'cors'
      })
    ));
    return true;
  } catch (e) {
    console.warn("Delete fallback failed", e);
    return false;
  }
}
// ---------------- Utils ----------------
function pickLatestByCreated(arr, n = 5) {
  return (arr || [])
    .slice()
    .sort((a, b) => (b.createdAt?.getTime?.() || 0) - (a.createdAt?.getTime?.() || 0))
    .slice(0, n);
}

function monthKey(d) {
  // Return a sortable key but we'll display it formatted
  return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, "0")}`;
}

function formatMonthDisplay(monthStr) {
  // Convert "2025-01" to "Jan 2025" or "Jan '25"
  const [year, month] = monthStr.split('-');
  const monthNames = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  const monthName = monthNames[parseInt(month) - 1];
  const shortYear = year.slice(2); // Get last 2 digits of year
  return `${monthName} '${shortYear}`;
}

function isSameDay(a, b) {
  return a.getFullYear() === b.getFullYear() && a.getMonth() === b.getMonth() && a.getDate() === b.getDate();
}
function toISODate(d) {
  if (!d) return "";
  const dt = new Date(d);
  const y = dt.getFullYear();
  const m = String(dt.getMonth() + 1).padStart(2, '0');
  const da = String(dt.getDate()).padStart(2, '0');
  return `${y}-${m}-${da}`;
}

