// ---------------- Lightweight runtime self-checks ("tests") ----------------
(function runSelfChecks() {
  try {
    console.assert(monthKey(new Date(2025, 0, 1)) === "2025-01", "monthKey should format YYYY-MM (Jan)");
    console.assert(monthKey(new Date(2025, 11, 1)) === "2025-12", "monthKey should format YYYY-MM (Dec)");

    const seeded = seedOpps();
    console.assert(Array.isArray(seeded) && seeded.length > 0, "seedOpps should return non-empty array");
    const shapeOk = seeded.every(o => typeof o.id === 'number' && typeof o.title === 'string' && STATUSES.includes(o.status));
    console.assert(shapeOk, "seedOpps items should have expected shape");

    const colorOk = STATUSES.every(s => Object.prototype.hasOwnProperty.call(STATUS_COLORS, s));
    console.assert(colorOk, "Each status should have a color mapping");

    console.assert([1, 2, 3].every(r => ALLOWED_ROWS.includes(r)), "ALLOWED_ROWS should include 1,2,3");
    console.assert([3, 4, 6, 8, 12].every(c => ALLOWED_COLS.includes(c)), "ALLOWED_COLS should include 3,4,6,8,12");
    console.assert(GAP_PX === 16, "GAP_PX should be 16");

    const datesOk = seeded.every(o => o.createdAt instanceof Date && o.closeDate instanceof Date);
    console.assert(datesOk, "seedOpps should emit Date objects for createdAt/closeDate");

    const now = new Date();
    const a = { createdAt: new Date(now.getTime() - 1000) };
    const b = { createdAt: new Date(now.getTime()) };
    const c = { createdAt: new Date(now.getTime() - 500) };
    const ordered = pickLatestByCreated([a, b, c], 3);
    console.assert(ordered[0] === b && ordered[1] === c && ordered[2] === a, "pickLatestByCreated should sort descending by createdAt");

    const d1 = new Date(2025, 0, 2);
    const d2 = new Date(2025, 0, 2, 23, 59, 59);
    const d3 = new Date(2025, 0, 3);
    console.assert(isSameDay(d1, d2) && !isSameDay(d1, d3), "isSameDay should compare Y/M/D only");

    const limited = pickLatestByCreated([a, b, c], 2);
    console.assert(limited.length === 2 && limited[0] === b && limited[1] === c, "pickLatestByCreated should return at most n items in order");
    console.assert(monthKey(new Date(2024, 1, 9)) === "2024-02", "monthKey should zero-pad month (Feb)");

    console.assert(/^\d{4}-\d{2}-\d{2}$/.test(toISODate(new Date())), "toISODate always returns YYYY-MM-DD");
    console.assert(toISODate(new Date(2024, 0, 5)) === "2024-01-05", "toISODate should format YYYY-MM-DD");
  } catch (e) {
    console.warn("Self-checks failed:", e);
  }
})();