const getApiBaseUrl = () => {
  if (window.location.hostname.includes('cfapps.us10-001.hana.ondemand.com')) {
    return 'https://dpfna-sbx-sbx-vector-backend.cfapps.us10-001.hana.ondemand.com/api';
  }
  if (window.location.hostname === 'localhost') {
    return 'http://localhost:5003/api';
  }
  return '/api';
};

export const API_BASE_URL = getApiBaseUrl();